.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto; /* to scroll the menu if needed */

  &__top {
    margin-bottom: 24px;
    position: sticky; /* to keep cross always visible */
    top: 0;
    z-index: 1;
  }

  &__nav {
    padding-bottom: 40px;
  }

  &_phone-number {
    display: block;
    margin-bottom: 16px;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &_book-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
